import React, { Component, Fragment } from 'react'

class ContentAbout extends Component {
    render() {
        return (
            <Fragment>
                <div className="row">
                    <div className="col-lg-7 col-md-7 col-sm-12">
                        <h2 className="sr-4">sobre ns</h2>
                        <h3 className="sr-3">Oi, sou a Geovanna Garcia</h3>
                        <p className="sr-1">Saber que seus bens esto seguros  viver com mais tranquilidade, podendo dedicar seu tempo no que mais importa para voc. Pensando nisso a LIEN Corretora de Seguros trabalha oferecendo facilidade e transparncia. Uma corretora atual que busca a melhoria contnua em seus processos e servios.</p>
                        <p className="sr-1">Nosso objetivo  proporcionar segurana e despreocupao, por isso contamos com uma consultoria especializada que visa orientar e apresentar a proposta de seguro mais adequada s suas necessidades, atravs de uma anlise minuciosa para oferecer o melhor custo-benefcio com a certeza de uma aplice bem elaborada.</p>
                        <p className="sr-1">No site  possvel solicitar cotaes, tirar dvidas e compreender em detalhes tudo sobre o seguro contratado. Isso permitir usufruir de todas as facilidades que o seguro oferece e ainda ter a certeza de estar realmente assistido, caso tenha a necessidade de acionar os servios. Com a LIEN Corretora de Seguros voc estar protegido e ter todo o suporte de nossa consultoria especializada com agilidade e clareza. Conhea nossos servios, faa um seguro com a LIEN.</p>
                        <img src={require('../../../assets/images/geovanna-icon-heart.svg')} className="sr-4" alt="cone de dois coraes" />
                        <h3 className="sr-3">A nossa misso</h3>
                        <p className="sr-1"> planejar com total dedicao o momento mais importante na vida de um casal, acompanhando todo processo com muito profissionalismo transmitindo tranquilidade e confiana para que os noivos possam desfrutar ao mximo esse momento nico e especial.</p>
                    </div>
                </div>
            </Fragment>
        );
    }
}

export default ContentAbout


