import React, { Component, Fragment } from 'react'
import Button from '../../Button'

class ContentAbout extends Component {
    render() {
        return (
            <Fragment>
                <div className="row">
                    <div className="col">
                        <h2 className="sr-2">Tipos de Servios</h2>
                        <img src={require('../../../assets/images/geovanna-icon-ring.svg')} className="sr-3" alt="cone de um anel de casamento"/>
                    </div>
                </div>
                <div className="row">
                    <div className="col-lg-6 col-md-6 col-sm-12">
                        <h3 className="sr-3">Assessoria<br/><span className="hl">Completa</span></h3>
                        <p className="sr-1">Nessa modalidade, iniciamos com a consultoria e seguimos acompanhando o casal durante todo o processo: elaborao do <i>layout</i>, estilo do casamento, busca por fornecedores, visitas tcnicas, contrataes e organizao de planilhas para o controle de oramento.</p>
                    </div>
                    <div className="col-lg-6 col-md-6 col-sm-12">
                        <h3 className="sr-3">Assessoria<br/><span className="hl">Intermediria</span></h3>
                        <p className="sr-1">Essa modalidade  para o casal que j contratou alguns servios, porm, encontraram dificuldades em organizar o casamento no meio do processo. Teremos reunies em que os noivos nos contam como ser o grande dia, e ento preparamos todo o suporte daqui pra frente.</p>
                    </div>
                </div>
                <a href="https://www.geovannagarcia.com.br/files/geovanna-apresentacao.pdf" className="sr-2" target="_blank" rel="noopener noreferrer" download>
                    <Button 
                        id="btn-presentation"
                        label="BAIXAR A APRESENTAO"
                        type="white">
                    </Button>
                </a>
            </Fragment>
        );
    }
}

export default ContentAbout


